import GET from '../server/server';

function projects(){

    const projectWrapper = document.querySelector('.projects__wrapper');

    function setProject(data){
        const datas = data.projects.map(({img, heading, usage, live, github}) =>{
            const div = document.createElement('div');
            div.classList.add('projects__list', 'wow', 'animate__fadeIn', 'animate__animated');

            div.innerHTML = `
            <div tabindex="0" class="projects__photo">
                <div class="projects__live">
                    <a target="_blank" tabindex="-1" href=${live}>Живой сайт</a>
                </div>
                <a target="_blank" tabindex="-1" href=${github} class="projects__code">Github code</a>
                <img src=${img} alt="project">
            </div>
            <h3 class="projects__heading">${heading}</h3>
            <div class="projects__usage">
            </div>
            `;

            const usageWrapper = div.querySelector('.projects__usage');

            const usageList = usage.map(item => {
                const li = document.createElement('span');
                li.textContent = `${item.work}`;
                usageWrapper.appendChild(li);
            });

            projectWrapper.appendChild(div);
        });
    }

    function gettingData(){
        GET('../../data.json')
            .then(data => setProject(data));
    }

    gettingData();
}

export default projects;