function scroll(){
    const links = document.querySelectorAll('a');

    for(let link of links){
        link.addEventListener('click', function(e){
            e.preventDefault();

            const href = link.getAttribute('href');
            console.log(href);
            const res =  document.querySelector('' + href);
            res.scrollIntoView({
                behavior: 'smooth',
                block: 'start'
            })
        });
    }

}

export default scroll;